export const required = value => {
    if(value) return undefined;
    return "Field is required";
};

export const maxLengthValidateCreator = length => {
    return value => {
        return value && value.length > length ? `Max length is ${length}` : undefined;
    }
};

export const minLengthValidateCreator = length => {
    return value => {
        return value && value.length < length ? `Min length is ${length}` : undefined;
    }
};

export const isEmail = value => {
    const re = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/g;
    if(re.test(value)) return undefined;
    return 'Please enter valid email. Example: example@mail.com'
};

export const isFullPhone = value => {
    if(value.indexOf('_') > 0) {
        return 'Please enter full phone';
    }
    return undefined;
};