import * as axios from "axios";

const instance = axios.create({
    baseURL: 'https://frontend-test-assignment-api.abz.agency/api/v1/'
});


export const getUsers = (page, pageSize) => {
    return instance.get(`users?page=${page}&count=${pageSize}`).then(response => response.data);
};

export const getPositions = () => {
    return instance.get('positions').then(response => response.data);
};

export const registerUser = (name, email, phone, position, photo, token) => {
    const formData = new FormData();
    formData.append('position_id', position);
    formData.append('name', name);
    formData.append('email', email);
    formData.append('photo', photo);
    formData.append('phone', phone);

    return instance.post('users', formData, {
        headers: {
            'Content-Type': 'multipart/form-data',
            'Token': token
        }
    }).then(response => response);

};

export const getToken = () => {
    return instance.get('token').then(response => response.data);
};