import {getPositions, getToken, registerUser} from "../api/api";
import {updateUsers} from "./usersRuducer";
import {stopSubmit} from "redux-form";

const initialState = {
    positions: [],
    error: null,
    isShowModal: false,
};

const SET_POSITIONS = 'formSectionReducer/SET_POSITIONS';
const SET_ERROR = 'formSectionReducer/SET_ERROR';
const SHOW_MODAL = 'formSectionReducer/SHOW_MODAL';

const formSectionReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_POSITIONS: {
            return {
                ...state,
                positions: action.payload,
            }
        }
        case SET_ERROR: {
            return {
                ...state,
                error: action.payload,
            }
        }
        case SHOW_MODAL: {
            return {
                ...state,
                isShowModal: action.payload
            }
        }
        default: return state;
    }
};

const setPositions = (payload) => ({
    type: SET_POSITIONS,
    payload,
});

const setError = (payload) => ({
    type: SET_ERROR,
    payload
});

export const showModal = (payload) => ({
    type: SHOW_MODAL,
    payload,
});

export const setPositionsAsyncAction = () => {
    return async (dispatch) => {
        try {
            const response = await getPositions();
            dispatch(setPositions(response.positions));
        }
        catch (e) {
            console.log(e);
        }
    }
};

export const registerUserAsyncAction = (name, email, phone, position, photo) => {
    return async (dispatch) => {
        try {
            const tokenResponse = await getToken();
            if(tokenResponse.success) {
                const response = await registerUser(name, email, phone, position, photo, tokenResponse.token);
                debugger;
                if (response.data.success) {
                    dispatch(updateUsers());
                    dispatch(showModal(true));
                }
            }
        } catch (e) {
            if(e.response.status === 422) {
                const fails = e.response.data.fails;
                const errorValues = Object.keys(fails).map(fail => fails[fail][0]);
                const errors = {};
                Object.keys(fails).forEach((key, i) => errors[key] = errorValues[i]);
                const action = stopSubmit('regForm', errors);
                dispatch(action);
            }
            if (e.response.status === 409) {
                dispatch(setError(e.response.data.message));
            }
        }
    }
};

export default formSectionReducer;