import React from 'react';
import PropTypes from 'prop-types';
import Avatar from "@material-ui/core/Avatar";
import styles from './User.module.css'
import Tooltip from "@material-ui/core/Tooltip";

const User = ({name, email, phone, position, photo}) => {
    return (
        <div className={styles.container}>
            <Avatar className={styles.avatar} alt={name} src={photo}/>
            <div className={styles.text}>
                <Substring style={styles.name} string={name}/>
                <div className={styles.description}>
                    <p>{position}</p>
                    <Substring string={email}/>
                    <p>{phone}</p>
                </div>
            </div>
        </div>
    );
};

const Substring = ({string, style}) => {
    if (string && string.length > 23) {
        return <Tooltip title={string}>
            <p className={style} style={{cursor: "pointer"}} >{`${string.substr(0,23)}...`}</p>
        </Tooltip>
    }
    return <p className={style}>{string}</p>
};

User.propTypes = {
    name: PropTypes.string,
    email: PropTypes.string,
    phone: PropTypes.string,
    position: PropTypes.string,
    photo: PropTypes.string
};

export default User;