import React from 'react';
import Container from "@material-ui/core/Container";
import {Field, reduxForm} from "redux-form";
import {useDispatch, useSelector} from "react-redux";
import ScrollableAnchor from 'react-scrollable-anchor'

import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";

import styles from './FormSection.module.css'
import textStyles from '../../styles/Text.module.css'
import buttonStyles from '../../styles/Buttons.module.css'

import {getError, getPositions} from "../../redux/formSectionSelector";
import {registerUserAsyncAction, setPositionsAsyncAction} from "../../redux/formSectionReducer";
import {
    isEmail,
    isFullPhone,
    maxLengthValidateCreator,
    minLengthValidateCreator,
    required
} from "../../helpers/validators";
import Modal from "./Modal/Modal";
import RadioButtons from "./RadioButtons/RadioButtons";
import TextField from "./TextField";
import FileInput from "./FileInput";

const FormSection = () => {

    const dispatch = useDispatch();
    const onSubmit = ({name, email, phone, position, photo}) => {
        dispatch(registerUserAsyncAction(name, email, phone, position, photo));
    };

    return (
        <section>
                <Container className={styles.formContainer}>
                    <ScrollableAnchor id={'signup'}>
                        <h2 className={textStyles.title} >Register to get a work</h2>
                    </ScrollableAnchor>
                    <p className={textStyles.subtitle}>Attention! After successful registration and alert, update the list of users in the block from the top</p>
                    <Form onSubmit={onSubmit} initialValues={{position: '1'}}/>
                </Container>
                <Modal/>
        </section>
    );
};

const Form = reduxForm({form: 'regForm'})(({handleSubmit}) => {

    const dispatch = useDispatch();
    const error = useSelector(state => getError(state));

    React.useEffect(() => {
        dispatch(setPositionsAsyncAction())
    },[dispatch]);

    const positions = useSelector(state => getPositions(state));
    const maxlength60 = maxLengthValidateCreator(60);
    const minlength2 = minLengthValidateCreator(2);


    return (
        <form className={styles.form} onSubmit={handleSubmit}>
            <Grid container>
                <Grid item className={styles.item} xs={12}><Field validate={[required, maxlength60, minlength2]} component={TextField} label={'Name'} name={'name'} placeholder={'Your name'}/></Grid>
                <Grid item className={styles.item} xs={12}><Field validate={[required, isEmail]} component={TextField} label={'Email'} name={'email'} placeholder={'Your email'}/></Grid>
                <Grid item className={styles.item} xs={12}>
                    <Field
                        validate={[required, isFullPhone]}
                        component={TextField}
                        label={'Phone number'}
                        name={'phone'}
                        placeholder={'+380 XX XXX XX XX'}
                        sublabel={'Enter phone number in open format'}
                        mask="+380999999999"
                    />
                </Grid>
                <Grid className={styles.item} item xs={12}>
                    <Field component={RadioButtons}  positions={positions} name={'position'}/>
                </Grid>
                <Grid className={styles.item} item xs={12} ><Field
                    type="file"
                    name="photo"
                    component={FileInput}
                    validate={[required]}
                /></Grid>
                <div className={styles.buttonContainer}>
                    {error && <p className={styles.inputErrorText}>{error}</p>}
                    <Button className={`${buttonStyles.button} ${buttonStyles.buttonContained}`}  fullWidth type={'submit'}>Sign up now</Button>
                </div>
            </Grid>
        </form>
    )
});

export default FormSection;