import {getUsers} from "../api/api";

const initialState = {
    users: [],
    page: 1,
    isFetching: false,
    isNextPage: false,
    error: null,
    isNeedFetchUsers: true,
};

const SET_USERS = 'usersReducer/SET_USERS';
const SET_NEXT_PAGE = 'usersReducer/SET_NEXT_PAGE';
const TOGGLE_FETCHING_USERS = 'usersReducer/TOGGLE_FETCHING_USER';
const SET_IS_NEXT_PAGE = 'usersReducer/SET_IS_NEXT_PAGE';
const SET_ERROR = 'usersReducer/SET_ERROR';
const UPDATE_USERS = 'usersReducer/UPDATE_USERS';


const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USERS: {
            return {
                ...state,
                users: [...state.users, ...action.payload],
                isNeedFetchUsers: false,
            }
        }

        case SET_NEXT_PAGE: {
            return {
                ...state,
                page: state.page + 1,
                isNeedFetchUsers: true
            }
        }

        case TOGGLE_FETCHING_USERS: {
            return {
                ...state,
                isFetching: action.payload,
            }
        }

        case SET_IS_NEXT_PAGE: {
            return {
                ...state,
                isNextPage: action.payload,
            }
        }

        case SET_ERROR: {
            return {
                ...state,
                error: action.payload,
            }
        }

        case UPDATE_USERS: {
            return {
                ...state,
                page: 1,
                users: [],
                isNeedFetchUsers: true,
            }
        }

        default: return state;
    }
};

const setUsers = (payload) => ({
    type: SET_USERS,
    payload,
});

export const setNextPage = () => ({
    type: SET_NEXT_PAGE,
});

const toggleFetchingUsers = (payload) => ({
    type: TOGGLE_FETCHING_USERS,
    payload,
});

const setIsNextPage = (payload) => ({
    type: SET_IS_NEXT_PAGE,
    payload
});

const setError = (payload) => ({
    type: SET_ERROR,
    payload,
});

export const updateUsers = () => ({
    type: UPDATE_USERS,
});

export const getUsersAsyncAction = (page, pageSize) => {
    return async (dispatch) => {
        dispatch(toggleFetchingUsers(true));
        try {
            const response =  await getUsers(page, pageSize);
            if (response.success){
                await dispatch(setUsers(response.users));
                response.links.next_url ? dispatch(setIsNextPage(true)) : dispatch(setIsNextPage(false));
                dispatch(toggleFetchingUsers(false));
                dispatch(setError(null));
            }
            else {
                dispatch(toggleFetchingUsers(false));
                dispatch(setError(response.message));
            }
        } catch (e) {
            dispatch(setError(e.message));
            dispatch(toggleFetchingUsers(false));
        }

    }
};

export default usersReducer;