import React from 'react';
import Container from "@material-ui/core/Container";
import DesctopNavBar from "../NavBar/DesktopNavBar";
import Grid from "@material-ui/core/Grid";
import AppBar from "@material-ui/core/AppBar";
import logo from '../../assets/logo.svg'
import styles from './Header.module.css'
import Link from "@material-ui/core/Link";
import {Hidden} from "@material-ui/core";
import MobileNavBar from "../NavBar/MobileNavBar";
import {goToTop} from "react-scrollable-anchor";

const Header = () => {

    const onLogoClickHandler = () => {
      goToTop()
    };

    return (
        <AppBar color={'inherit'}>
            <Container>
                <Grid container className={styles.content}>
                    <Grid item xs={5}><Link onClick={onLogoClickHandler}><img className={styles.logo} alt={'logo'} src={logo}/></Link></Grid>
                    <Grid item xs={7}>
                        <Hidden mdUp><MobileNavBar logoStyle={styles.logo}/></Hidden>
                        <Hidden smDown><DesctopNavBar/></Hidden>
                    </Grid>
                </Grid>
            </Container>
        </AppBar>
    );
};

export default Header;