import React from 'react';
import Dialog from "@material-ui/core/Dialog";
import Slide from "@material-ui/core/Slide";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import {useDispatch, useSelector} from "react-redux";
import {showModal} from "../../../redux/formSectionReducer";
import {getIsShowModal} from "../../../redux/formSectionSelector";
import buttonStyles from '../../../styles/Buttons.module.css'
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from '@material-ui/icons/Close';
import styles from './Modal.module.css'

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});







const Modal = () => {

    const dispatch = useDispatch();
    const isShowModal = useSelector(state => getIsShowModal(state))

    const handleClose = () => {
        dispatch(showModal(false));
    };

    return (
        <div>
            <Dialog
                open={isShowModal}
                TransitionComponent={Transition}
                keepMounted
                onClose={handleClose}
            >
                <IconButton aria-label="close" className={styles.closeButton} onClick={handleClose}>
                    <CloseIcon />
                </IconButton>
                <p className={styles.title} >{"Congratulations"}</p>
                <div className={styles.content}>
                    <p>
                        You have successfully passed the registration
                    </p>
                </div>
                <DialogActions className={styles.btns}>
                    <div className={styles.buttonContainer}>
                        <Button onClick={handleClose} fullWidth className={`${buttonStyles.button} ${buttonStyles.buttonContained}`}>
                            Great
                        </Button>
                    </div>
                </DialogActions>
            </Dialog>
        </div>
    );
};

export default Modal;