import React from 'react';
import Container from "@material-ui/core/Container";
import styles from './AboutMe.module.css'
import textStyles from '../../styles/Text.module.css'
import buttonStyles from '../../styles/Buttons.module.css'
import Grid from "@material-ui/core/Grid";
import laptopSvg from '../../assets/man-laptop-v1.svg'
import Button from "@material-ui/core/Button";
import ScrollableAnchor from "react-scrollable-anchor";

const AboutMe = () => {
    return (
        <section className={styles.wrapper}>
            <Container>
                <div className={styles.titleContainer} >
                    <ScrollableAnchor id={'aboutme'}><h2 className={textStyles.title}>Let's get acquainted</h2></ScrollableAnchor>
                </div>
                <Grid className={styles.content} container>
                    <Grid item xs={12} sm={4}  md={5} lg={5} xl={5} className={styles.imgContainer}><img className={styles.img}  alt={'laptop-svg'} src={laptopSvg}/></Grid>
                    <Grid className={styles.textContainer} item xs={12} sm={7} md={6} lg={6} xl={6}>
                        <h3 className={styles.textContainerTitle}>I am cool frontend developer</h3>
                        <p className={styles.textContainerDescription}>
                            We will evaluate how clean your approach to writing CSS and Javascript code is.You can use any CSS and Javascript 3rd party libraries without any restriction.

                        </p>
                        <p className={styles.textContainerDescription} >
                            If 3rd party css/javascript libraries are added to the project via bower/npm/yarn you will get bonus points.
                            If you use any task runner (gulp/webpack) you will get bonus points as well.
                            Slice service directory page P​SD mockup​ into HTML5/CSS3.
                        </p>
                        <div className={styles.buttonContainer}><Button href={'#signup'} className={buttonStyles.button}>Sign up now</Button></div>
                    </Grid>
                </Grid>
            </Container>
        </section>
    );
};

export default AboutMe;