import React from 'react';
import SwipeableDrawer from "@material-ui/core/SwipeableDrawer";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from '@material-ui/icons/Menu';
import styles from './MobileNavBar.module.css'
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import logo from '../../assets/logo.svg'
import Divider from "@material-ui/core/Divider";



const MobileNavBar = ({logoStyle}) =>  {
    const [open, setOpen] = React.useState(false);

    const toggleDrawer = (open) => (event) => {
        if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        setOpen(open);
    };


    return (
                <div className={styles.container}>
                    <IconButton className={styles.iconBtn} onClick={toggleDrawer(true)}><MenuIcon color={'primary'}/></IconButton>
                    <SwipeableDrawer anchor={'left'} open={open} onClose={toggleDrawer(false)} onOpen={toggleDrawer(true)}  >
                        <List  onKeyDown={toggleDrawer(false)} onClick={toggleDrawer(false)}>
                            <ListItem className={styles.logoItem}><img className={logoStyle} alt={'logo'} src={logo}/></ListItem>
                            <Divider className={styles.divider}/>
                            <div className={styles.menu}>
                                <ListItem className={styles.menuItem}>About me</ListItem>
                                <ListItem className={styles.menuItem}>Relationships</ListItem>
                                <ListItem className={styles.menuItem}>Users</ListItem>
                                <ListItem className={styles.menuItem}>Sign Up</ListItem>
                                <ListItem className={styles.menuItem}>Terms and Conditions</ListItem>
                            </div>
                            <Divider className={styles.divider}/>
                            <div className={styles.menu}>
                                <ListItem className={styles.menuItem}>How it works</ListItem>
                                <ListItem className={styles.menuItem}>Partnership</ListItem>
                                <ListItem className={styles.menuItem}>Help</ListItem>
                                <ListItem className={styles.menuItem}>Leave testimonial</ListItem>
                                <ListItem className={styles.menuItem}>Contact us</ListItem>
                            </div>
                            <Divider className={styles.divider}/>
                            <div className={styles.menu}>
                                <ListItem className={styles.menuItem}>Articles</ListItem>
                                <ListItem className={styles.menuItem}>Our news</ListItem>
                                <ListItem className={styles.menuItem}>Testimonials</ListItem>
                                <ListItem className={styles.menuItem}>Licenses</ListItem>
                                <ListItem className={styles.menuItem}>Privacy Policy</ListItem>
                            </div>

                        </List>
                    </SwipeableDrawer>
                </div>
    );
};

export default MobileNavBar;
