import React from 'react';

import {Hidden, Container, Grid, Button} from "@material-ui/core";

import styles from './Banner.module.css'
import buttonStyles from '../../styles/Buttons.module.css'


const Banner = () => {
    return (
        <section className={styles.wrapper}>
            <Container className={styles.container}>
                <div className={styles.bannerText}>
                    <Grid item xs={12} sm={7} lg={6} xl={6} >
                        <h1 className={styles.title}>
                            Test assignment for Frontend Developer position
                        </h1>
                        <Hidden xsDown>
                            <p className={styles.description}>
                                We kindly remind you that your test assignment should be submitted as a link to github/bitbucket repository. Please be patient, we consider and respond to every application that meets minimum requirements. We look forward to your submission. Good luck! The photo has to scale in the banner area on the different screens
                            </p>
                        </Hidden>
                        <Hidden smUp>
                            <p className={styles.description}>
                                We kindly remind you that your test assignment should be submitted as a link to github/bitbucket repository.
                            </p>
                        </Hidden>
                        <div className={styles.buttonContainer}><Button fullWidth href={'#signup'} className={`${buttonStyles.button} ${buttonStyles.buttonContained}`}>Sing up now</Button></div>
                    </Grid>
                </div>
            </Container>
        </section>
    );
};

export default Banner;