import styles from "./FormSection.module.css";
import InputMask from "react-input-mask";
import React from "react";

const TextField = ({label, input, placeholder, type, sublabel, mask, meta: {touched, error}}) => {
    return (
        <label className={styles.inputContainer}>
            <span>{label}</span>
            <InputMask className={`${styles.input} ${error && touched ? styles.inputError : ''}`} {...input} mask={mask}   placeholder={placeholder} type={type}  required/>
            {sublabel && <p className={styles.sublabel}>{sublabel}</p>}
            <p className={styles.inputErrorText}>{ touched && error}</p>
        </label>
    )
};

export default TextField;