import React from "react";
import styles from "./FormSection.module.css";

const FileInput  = (
    {
        input: {onChange, onBlur, value: omitValue, ...inputProps},
        meta: {error, touched, invalid},
        ...props
    }
    ) =>
    {

        const [fileName , setFileName] = React.useState('Upload your photo');

        const handleChange = (handler) => ({target: {files}}) => {
            handler(files.length ?  files[0] : '');
            setFileName(files.length ? files[0].name : 'Upload your photo');
        };

        return <>
            <label>Photo</label>
            <label className={`${styles.inputLabel} ${error && touched ? styles.inputError : ''}`} htmlFor="file">
                <div className={styles.inputLabelDesc}>{fileName}</div>
                <div className={`${styles.inputLabelBtn} ${error && touched ? styles.inputError : ''}`}>Browse</div>
                <input
                    id={'file'}
                    className={ styles.fileInput}
                    type="file"
                    onChange={handleChange(onChange)}
                    onBlur={handleChange(onBlur)}
                    accept="image/jpg, image/jpeg"
                    required
                    {...inputProps}
                    {...props}
                />
            </label>
            <p className={styles.inputErrorText}>{touched && error}</p>
        </>
    }
;

export default FileInput;